@use "./scss/" as *;
@import '~antd/dist/antd.css';
@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;500;600;700;900&display=swap");

* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    -webkit-tap-highlight-color: transparent;
}

html {
    font-size: 100%;
    @include tablet {
        font-size: 80%;
    }
    @include mobile {
        font-size: 60%;
    }
}

body {
    font-family: $font-family;
    font-weight: 400;
    line-height: 1.5;

    background-color: $body-bg;
    color: $txt-color;

    @include mobile {
        margin-bottom: 3rem;
    }
}
.themeClient {
    background-color: $body-bg;
    color: $txt-color !important;
    a{
        color: white !important;
    }
}

a {
    text-decoration: none !important;
    color: unset;
}


button,
input {
    outline: 0;
}

ul {
    list-style-type: none;
}

img {
    max-width: 100%;
}

iframe {
    border: 0;
}

.containerHome {
    max-width: 1660px;
    margin: auto;
}

.mb-3 {
    margin-bottom: 3rem;
}

.mb-2 {
    margin-bottom: 2rem;
}

.section {
    padding: 0 2rem;
}

.section__header {
    @include flex(center, space-between);
}
.wrapper {
    position: relative;
    display: flex;
    height: 100%;
    background-color: #eceff3;
}

